import Vue from 'vue'
import Vuex from 'vuex'
import io from 'socket.io-client'
Vue.use(Vuex);
export default new Vuex.Store({
  mode:'history',
  state:{
    uname:'sss',
  },
  mutations:{
    connectSuccess(state,data){
      alert('链接成功'+data)
    }
  },
  actions:{
    connectSocket({commit}){
      let socket=io.connect();
      socket.emit('login',this.state.uname);
      socket.on('loginSuccess',function (arr) {
        console.log('socket已连接')
      });
      socket.on('loginOutSuccess',function (arr) {
        console.log('socket断开连接')
      })
      // let ws=new WebSocket('ws://localhost:4000');
      // ws.onopen=function(){
      //   alert(222)
      //   ws.send('11111')
      //   commit('connectSuccess','aaaa')
      // }
    }
  }
})



