<template>
  <div class="login">
    <img src="../assets/imgages/loginbg2.jpg" alt="">
    <div class="login-warp">
      <div class="input-group">
        <label for="username">账号:</label>
        <el-input v-model="username" id="username"></el-input>
      </div>
      <div class="input-group">
        <label for="password">密码:</label>
        <el-input v-model="password" id="password" type="password"></el-input>
      </div>
      <div class="input-group clearfix">
        <input ref="remember" id="remember" class="fl" type="checkbox" @change="change"/>
        <label for="remember" class="fl">记住密码</label>
        <a class="forget fr" href="javascript:;">忘记密码</a>
      </div>
      <div class="input-group">
        <el-button :disabled="isLoading" :loading="isLoading" type="primary" @click="login">登录</el-button>
      </div>
      <div class="input-group">
        <el-button type="primary">注册</el-button>
      </div>
    </div>
  </div>
</template>
<script>
  import {setCookie,getCookie} from "../util/common";
  export default {
        name:'login',
        data: function () {
            return {
              isLoading:false,
              username:'',
              password:'',
            };
        },
        mounted(){
          if(!getCookie('isRemember')){
            this.$refs.remember.checked=true;
            this.username=getCookie('username');
            this.password=getCookie('password');
          }
          //this.$store.dispatch('connectSocket')
        },
      methods:{
        change(){
          this.$refs.remember.checked=!this.$refs.remember.checked;
          setCookie('isRemember',this.$refs.remember.checked,365);
        },
        login(){
          if(getCookie('isRemember')){
            setCookie('username',this.username,365);
            setCookie('password',this.password,365);
          }
          if(this.username==''||this.password==""){
            this.$message('账号和密码不能为空');
            return;
          }
          this.isLoading=true;
          this.$http.post('login.do',{username:this.username,password:this.password}).then(res=>{
            this.isLoading=false;
            if(res.data.success){
              this.$router.push({name:'home'})
            }
          })
        }
      }
    }
</script>
<style lang="less">
  body{
    overflow: hidden;
  }
  .login{
    font-size: 14px;
    img{
      width: 100%;
    }
    .login-warp{
      width: 20%;
      padding: 20px 20px 0 20px;
      border:1px solid black;
      border-radius: 6px;
      //margin: 0 auto;
      position: absolute;
      left: 40%;
      top: calc(~'50% - 150px');
      .input-group{
        margin-bottom: 20px;
        width: 100%;
        vertical-align: middle;
        input{
          vertical-align: middle;
        }
        .el-input{
          width:calc(~'100% - 36px');
          .el-input__inner{
            height: 34px;
          }
        }
        .el-button{
          width: 100%;
        }
        .forget{
          color: #3a8ee6;
        }
      }
    }
  }

</style>



